<template>
	<div class="ind-recnews mt5 bg-fff pl3 pr3 pb3">
		<h1 class="recnews-title">今日头条</h1>
		<div class="rec-newlist mt3">
			<div class="news-box mb3" v-for="(o,index) in recnews" v:bind:item="o" v-bind:index="index">
				<router-link :to="{path:'/newsdetail',query: {id: index}}">
					<h2 class="news-btitle">{{ o.title }}</h2>
					<div class="news-bimg clearfix">
						<div class="imgso fl"><img class="imgblock" :src="o.image"></div>
					</div>
					<div class="new-other  pt4 pb4 clearfix">
						<span class="newoth-source fl">来源：{{ o.name }}</span>
						<span class="newoth-time fr">时间：{{ o.day }}</span>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default{
		data(){
			return{
				recnews:[]
			}
		},
		mounted: function(){
			let url = '/api/1443-1';
			let sign = 'FD23B802A29A46D14CE759F93EAC3125';
			axios({
				method: 'post',
				url: url,
				params: {
					'showapi_appid':'54164',
			    	'showapi_sign':sign
				}
			}).then((res) => {
				console.log(res.data.showapi_res_body.list)
				this.recnews = res.data.showapi_res_body.list
			}) 
		}
	}
</script>

<style>
</style>


