<template>
	<div class="newsdetail pt5 pr5 pb5 pl5">
		<header-nav></header-nav>
		<!--<div class="del-box clearfix mb5">
			<input class="del-input fl" type="text" name="number" placeholder="请输入解析的网址" autocomplete="off" v-model="newsurl">
			<button class="del-btn fl" v-on:click="newsctn">搜索</button>
		</div>-->
		<h1 class="news-title">《我的世界》小羊换多彩新造型，这样的羊你见过没？</h1>
		<div class="news-titother mt5 mb5 tc">
			<span class="tit-source">来源：<i>头条新闻</i></span>
			<span class="tit-data">时间：2018.1.16</span>
			<span class="tit-data">文章id:{{ this.$route.query.id }}</span>
		</div>
		<div class="news-images">
			<img src="../../assets/images/ad/newsad1.jpg">
		</div>
		<div class="newscontent mt4">
			<div><p>在《我的世界》里冒险家们可以通过更换各种颜色的护甲来变换造型，其实《我的世界》里有些生物也会去换新的造型，而且背后还有他们悲伤或者烦恼的故事。</p><p><strong>小羊篇</strong></p><p>小羊一号称她虽然有着茂密的毛发，可却只有单调的颜色，很想要青春靓丽一点的毛色。</p></div></div>
	</div>
</template>

<script>
	import axios from 'axios'
	import HeaderNav from '../../components/header.vue'
	export default{
		components : {
			HeaderNav
		},
		data(){
			return{
				id:'',
				newsdata:[],
				newsurl:''
			}
		},
		mounted: function(){
		    document.getElementsByTagName("body")[0].className="bgfff"
		    this.id = this.$route.query.id
		},
		methods:{
			newsctn(){
				if( this.newsurl=='' || this.newsurl==null ){
					alert("请输入要解析的网址！")
					return
				}
				let url = '/api/644-1';
				let sign = 'bc41dbe15d504543b791fe0cc5a80e50';
				axios({
					method: 'post',
					url: url,
					params: {
						'showapi_appid':'54164',
				    	'showapi_sign':sign,
				    	'url':this.newsurl
					}
				}).then((res) => {
					console.log(res)
					//this.newsdata = res.data.showapi_res_body.list
				}) 
			}
		},
		beforeDestroy () {
		    document.getElementsByTagName("body")[0].className="";
		}
	}
</script>

<style scoped>
	.newsdetail{}
	.newsdetail .news-title{color: #000;font-size: 22px;line-height: 1.45rem;font-weight: 600;}
	.news-titother{}
	.news-titother .tit-source{display: inline-block; margin: 0 .4rem; font-size: .7rem;}
	.news-titother .tit-data{color: #888888;display: inline-block; margin: 0 .4rem;}
	.news-titother .tit-source i{color: #519aed;}
	.news-images img,.newscontent img{max-width: 100%;}
	.newscontent{position: relative;overflow: hidden;clear: both;padding-bottom: 24px;}
	
</style>


